<script>
import navbar from '@/components/_navbar'
import footer_component from '@/components/_footer'
import hero from '@/components/_hero'

export default {
    name: 'team_register',
    components: {
        navbar,
        footer_component,
        hero
    },
    data: function(){
        return {
            team: {},
            new_team: {
                name: '',
                email: '',
                password: '',
                reputation: ''
            },
            new_leader: {
                member_name: '',
                member_phone: '',
                member_email: '',
                member_birthdate: '',
                member_gender: '',
                member_job: '',
                member_nationality: '',
                member_country: '',
                member_city: '',
                member_address: '',
                member_zip: ''
            },
            // new_members: [],
            // membersFormCounter: 1,
            // active_contests: [],
            api_url: my_api.url
        }
    },
    mounted: function(){
        this.initiDatepicker()
        this.initiSelect2()
    },
    created: function(){
        // for(let i = 0; i < 100; i++){
        //     this.new_members.push({
        //         member_name: '',
        //         member_phone: '',
        //         member_email: '',
        //         member_birthdate: '',
        //         member_gender: '',
        //         member_job: '',
        //         member_nationality: ''
        //     })
        // }

        // this.$http.get(my_api.url+'/json/activeContests')
        //     .then(res => {
        //         this.active_contests = res.body
        //         console.log(this.active_contests)
        //     })
    },
    methods: {
        saveTeamDetails: function(){
            // validate data
            if(
                !this.new_team.name ||
                !this.new_team.email ||
                !this.new_team.password ||
                !this.validateEmail(this.new_team.email)
            ){
                this.showError()
            }else{
                $('.team-form-warpper .loader').fadeIn('fast')
                this.$http.post(my_api.url+'/teams/register', this.new_team)
                    .then(res => {
                        if(res.body.success === true){
                            localStorage.setItem('team_id', res.body.team.team_id)
                            this.showSuccess()
                            this.nextStep()
                        }else{
                            console.log(res.body)
                            this.showError()
                        }
                    })
                    .catch(err => {
                        console.log('ERROR ', err)
                        this.showError()
                    })
            }

        },

        saveLeaderDetails: function(){
            // validate data
            if(
                !this.new_leader.member_name ||
                !this.new_leader.member_phone ||
                !this.new_leader.member_email ||
                !this.new_leader.member_birthdate ||
                !this.new_leader.member_gender ||
                !this.new_leader.member_job ||
                !this.new_leader.member_nationality ||
                !this.new_leader.member_country ||
                !this.new_leader.member_city ||
                !this.new_leader.member_address ||
                !this.new_leader.member_zip
            ){
                this.showWarning()
            }else{
                $('.team-form-warpper .loader').fadeIn('fast')
                // adding team_id
                this.new_leader.member_team_id = localStorage.getItem('team_id')

                this.$http.post(my_api.url+'/members/register/leader', this.new_leader)
                    .then(res => {
                        if(res.body.success === true) {
                            this.showSuccess()
                            this.auth()
                            console.log('leader is done')
                        }
                    })
                    .catch(err => {
                        console.log('ERROR ', err)
                        this.showError()
                    })
            }

        },

        auth: function(){
            const self = this;
            let team = {
                email: this.new_team.email,
                password: this.new_team.password
            }
            this.$http.post(this.api_url+'/teams/login', team)
                .then(res =>{
                    console.log(res.body.team)
                    if( res.body.token ){
                        localStorage.setItem('team_token', res.body.token)
                        localStorage.setItem('team_id', res.body.team._id)
                        localStorage.setItem('team_custom_id', res.body.team.custom_id)
                        localStorage.setItem('team_name', res.body.team.team_name)
                        location.reload()
                        this.$router.push('/profile')
                    }else{
                        console.log('err login')
                        self.showError()
                    }
                })
                .catch(err => {
                    console.log('Error: ', err)
                    console.log('What the hell man!')
                    self.showError()
                })
        },

