<template>
  <div class="part-navbar">
    <!-- Header -->
    <header id="header" class="alt">
        <h1><a href="#">
            <img src="/static/images/logo.png" alt="Logo">
            <span class="txt">Grintos</span>
        </a></h1>
        <nav>
            <span class="is-logged-in" v-if="name">Welcome, {{name}}</span>
            <a href="#menu">Menu</a>
        </nav>
    </header>

    <!-- Menu -->
    <nav id="menu">
        <div class="inner">
            <h2>Menu</h2>
            <ul class="links">
                <li><router-link to="/" exact>
                    Home
                    <i class="icon ion-home"></i>
                </router-link> </li>
                <li><router-link to="/competitions" exact>
                    COMPETITIONS
                    <i class="icon ion-ios-list"></i>
                </router-link> </li>
                <li v-if="token"><router-link to="/profile" exact>
                    Profile
                    <i class="icon ion-person"></i>
                </router-link> </li>
                <li v-if="token"><router-link to="/logout" exact>
                    Logout
                    <i class="icon ion-log-out"></i>
                </router-link> </li>
                <li v-if="!token"><router-link to="/login" exact>
                    Login
                    <i class="icon ion-log-in"></i>
                </router-link> </li>
                <li v-if="!token"><router-link to="/team_register" exact>
                    Register Team
                    <i class="icon ion-person-stalker"></i>
                </router-link> </li>
            </ul>
            <a href="#" class="close">Close</a>
        </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'navbar',
  data () {
    return {
      token: '',
      name: ''
    }
  },
  created: function(){
    this.token = localStorage.getItem('team_token')
    this.name = localStorage.getItem('team_name')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.is-logged-in{
    margin-right: 10px
}
#header h1 a img{
    width: 40px;
    vertical-align: middle;
}
ul.links{
    text-align: left;
}
ul.links a{
    padding-left: 10px !important;
    padding-right: 10px !important;
}
ul.links a i{
    float: right;
    font-size: 22px
}
</style>



