<template>
    <div class='page-login'>
        <navbar></navbar>
        <!--
        <hero
            url="/static/images/wallpapers/login.jpg"
            text="Login"
            icon="/static/images/contest_icon/login.svg"
            ></hero>

        <!-- Wrapper -->
        <section id="wrapper">
          <!-- One -->
          <section class="login-form">
            <div class="alert alert-danger" style="margin-bottom: 20px; display: block;" v-if="show_err">Something wrong, please check the details again.</div>
            <span class="lock"><i class="icon ion-key"></i></span>
              <form v-on:submit="auth($event)" class="form">
                <label for="username">Team email</label>
                <input v-model="team.email" type="email" id="username">
                <label for="password">Password</label>
                <input v-model="team.password" type="password" id="password">
                <button type="submit" class="button">Login</button>
              </form>
          </section>


        </section>
        <!-- <footer_component></footer_component> -->
    </div>
</template>

<script>
import navbar from './_navbar'
import footer_component from './_footer'
import hero from './_hero'

export default {
    name: 'login',
    components: {
        navbar,
        footer_component,
        hero
    },
    data: function(){
        return {
            team: {},
            api_url: my_api.url,
            show_err: false
        }
    },
    beforeCreate: function() {
        document.body.className = 'login-page';
    },
    created: function(){
        localStorage.removeItem('team_token')
        localStorage.removeItem('team_id')
        localStorage.removeItem('team_custom_id')
        console.log('Old token removed!')
    },
    beforeDestroy: function(){
        document.body.className = '';
    },
    methods: {
        auth: function(e){
            e.preventDefault()
            const self = this;
            this.$http.post(this.api_url+'/teams/login', this.team)
                .then(res =>{
                    console.log(res.body.team)
                    if( res.body.token ){
                        localStorage.setItem('team_token', res.body.token)
                        localStorage.setItem('team_id', res.body.team._id)
                        localStorage.setItem('team_custom_id', res.body.team.custom_id)
                        localStorage.setItem('team_name', res.body.team.team_name)
                        location.reload()
                        this.$router.push('/profile')
                    }else{
                        console.log('err login')
                        self.show_err = true;
                        setTimeout(() => {
                            self.show_err = false;
                        }, 3000)
                    }
                })
                .catch(err => {
                    console.log('Error: ', err)
                    console.log('What the hell man!')
                    self.show_err = true;
                    setTimeout(() => {
                        self.show_err = false;
                    }, 3000)
                    // this.showError = true;
                    // var self = this;
                    // setTimeout(function(){
                    //     self.showError = false;
                    // }, 2100);
                })
                ;
        }
    }
}
</script>

<style scoped>
    .login-form{
        transform: translate(-50%, -80%);
        top: 50%;
        left: 50%;
        position: absolute;
        background-color: #fff
    }
    .login-form label{
        color: #333;
    }
    .login-form input{
        color: #333
    }
</style>



