<template>
  <div class="page-home">
    <navbar></navbar>
    <hero 
        url="/static/images/wallpapers/1.jpg"
        icon="/static/images/contest_icon/telescope.svg"
        text="Competitions"
        description="Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aspernatur, quas reiciendis unde aliquam dignissimos accusantium."
        ></hero>

      <!-- Wrapper -->
      <section id="wrapper">


      <!-- Four -->
          <section id="four" class="wrapper">
              <div class="inner">
                  <section class="features" v-if="all_contests.length > 0">
                      <article v-for="item in all_contests">
                            <router-link :to="{ path: '/single_contest', query: { id: item._id, title: item.title }}" class="image">
                                <img :src="api_url + '/uploads/contests/' + item.main_image[0].filename" alt="" />
                            </router-link>
                          <router-link :to="{ path: '/single_contest', query: { id: item._id, title: item.title }}">
                             <h3 class="major">{{item.title}}</h3>
                          </router-link>
                          <p>{{item.short_brief}}</p>
                          <router-link :to="{ path: '/single_contest', query: { id: item._id, title: item.title }}" class="special">See more</router-link>
                      </article>
                  </section>
                  <!-- <ul class="actions" v-if="all_contests.length > 0">
                      <li><a href="#" class="button">Browse All</a></li>
                  </ul>-->
                  <h3 v-else style="text-align: center; padding: 30px 0; font-size: 22px">No contest yet!</h3>
              </div>
          </section>

      </section>

    <!-- <footer_component></footer_component> -->
  </div>
</template>

<script>
import navbar from './_navbar'
import footer_component from './_footer'
import hero from './_hero'

export default {
  name: 'home',
  components: {
    navbar,
    footer_component,
    hero
  },
  data: function(){
      return { 
          all_contests: {},
          api_url: my_api.url
      }
  },
  created: function(){
      this.$http.get(my_api.url + '/json/contests')
        .then(res => {
            this.all_contests = res.body;
        })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



