<template>
    <div class='page-logout'>

    </div>
</template>

<script>
export default {
    name: 'logout',
    data: function(){
        return {
        }
    },
    created: function(){
        localStorage.removeItem('team_token')
        localStorage.removeItem('team_id')
        localStorage.removeItem('team_custom_id')
        localStorage.removeItem('team_name')
        
        location.reload()
        this.$router.push('/')
        console.log('Logged out!')
    }
}
</script>

<style>
</style>



