<template>
    <div class='page-profile'>
        <navbar></navbar>
        <hero
            url="/static/images/wallpapers/login.jpg"
            :text="team.team_name"
            icon="/static/images/contest_icon/login.svg"
            height="300"
            ></hero>
        <!-- Wrapper -->
        <section id="wrapper">
            <section id="one" class="wrapper style1" style="min-height: 100vh">
              <div class="inner">
                <h2 class="h-fix">Team details</h2>
                <!--
                <ul class="tabs">
                    <li @click="tab('members-details')" class="active">Members</li>
                    <li @click="tab('team-details')">Team</li>
                    <li @click="tab('team-contests')" >Contests</li>
                </ul>
                -->
                <div class="members-details tab">
                    <div class="cards-warpper clearfix">
                        <!--
                        <div class="single-member" v-for="item in members">
                            <span class="name"><i class="icon" :class="[item.member_gender === 'male' ? 'ion-male' : 'ion-female']"></i> {{item.member_name}}</span>
                            <span class="email"><i class="icon ion-email"></i> {{item.member_email}}</span>
                            <span class="phone"><i class="icon ion-ios-telephone"></i> {{item.member_phone}}</span>
                            <span class="date"><i class="icon ion-calendar"></i> {{item.member_birthdate}}</span>
                            <span class="date"><i class="icon ion-earth"></i> {{item.member_nationality}}</span>
                            <span class="date"><i class="icon ion-briefcase"></i> {{item.member_job}}</span>
                        </div>
                        -->

                        <div class="member-card expanded old_member_card" v-for="(item, i) in members" :class="{'king': item.member_is_leader}">
                            <div class="card-header clearfix">
                                <span class="card-title">
                                    {{item.member_name ? item.member_name : 'New member'}}
                                    {{item.member_job ? ' - ' + item.member_job : ''}}
                                </span>
                                <span class="card-btn expand-toggler plus" @click="expandCard($event)">
                                    <i class="icon ion-plus-round"></i>
                                    <i class="icon ion-minus-round"></i>
                                </span>
                                <span v-if="!item.member_is_leader" class="card-btn delete" @click="showDeleteBtns(true, $event)">
                                    <i class="icon ion-trash-a"></i>
                                </span>
                                <span class="card-btn edit" @click="editCard($event)">
                                    <i class="icon ion-edit"></i>
                                </span>
                            </div>
                            <div class="card-body">
                                <div class="loader"></div>
                                <div class="delete-btns">
                                    <button class="btn-delete" @click="deleteMember($event, item._id)">Delete?</button>
                                    <button class="btn-cancel-delete" @click="showDeleteBtns(false, $event)">Cancel</button>
                                </div>
                                <label>Member name <span class="req">*</span></label>
                                <input type="text" name="member_name" v-model="item.member_name" placeholder="full name" disabled>

                                <label>Member job <span class="req">*</span></label>
                                <input type="text" name="member_job" v-model="item.member_job" placeholder="job title" disabled>

                                <label>Member phone <span class="req">*</span></label>
                                <input type="text" name="member_phone" v-model="item.member_phone" placeholder="phone number" disabled>

                                <label>Member email <span class="req">*</span></label>
                                <input type="text" name="member_email" v-model="item.member_email" placeholder="email address" disabled>

                                <label>Member birthdate <span class="req">*</span></label>
                                <input
                                    type="text"
                                    v-model="item.member_birthdate"
                                    :data-object-index="i"
                                    data-toggle="datepicker"
                                    placeholder="dd/mm/yyyy" disabled
                                    name="member_birthdate"
                                    >

                                <label>Member gender <span class="req">*</span></label>
                                <div class="4u 12u$(small)">
                                    <input type="radio" :id="'member_male'+i" v-model="item.member_gender" :name="'member_gender'+i" value="male" disabled>
                                    <label :for="'member_male'+i">Male</label>
                                </div>
                                <div class="4u 12u$(small)">
                                    <input type="radio" :id="'member_female'+i" v-model="item.member_gender" :name="'member_gender'+i" value="female" disabled>
                                    <label :for="'member_female'+i">Female</label>
                                </div>

                                <label>Member nationality <span class="req">*</span></label>
                                <input type="text" name="member_nationality" v-model="item.member_nationality" placeholder="member primary nationality" disabled>

                                <!-- if leader -->
                                <div v-if="item.member_is_leader">
                                    <label>Member Country <span class="req">*</span></label>
                                    <input type="text" name="member_country" v-model="item.member_country" placeholder="member country" disabled>

                                    <label>Member City <span class="req">*</span></label>
                                    <input type="text" name="member_city" v-model="item.member_city" placeholder="member city" disabled>

                                    <label>Member zip code <span class="req">*</span></label>
                                    <input type="text" name="member_zip" v-model="item.member_zip" placeholder="member zip" disabled>

                                    <label>Member Address <span class="req">*</span></label>
                                    <input type="text" name="member_address" v-model="item.member_address" placeholder="member address" disabled>
                                </div>

                                <button class="update-member" @click="updateMember($event, item._id, item.member_is_leader)">Update member</button>
                            </div>
                        </div>

                        <div class="member-card expanded new_member_card" v-for="i in membersFormCounter">
                            <div class="card-header clearfix">
                                <span class="card-title">
                                    {{new_members[i].member_name ? new_members[i].member_name : 'New member'}}
                                    {{new_members[i].member_job ? ' - ' + new_members[i].member_job : ''}}
                                </span>
                                <span class="card-btn expand-toggler plus" @click="expandCard($event)">
                                    <i class="icon ion-plus-round"></i>
                                    <i class="icon ion-minus-round"></i>
                                </span>
                            </div>
                            <div class="card-body">
                                <label>Member name <span class="req">*</span></label>
                                <input type="text" v-model="new_members[i].member_name" placeholder="full name">

                                <label>Member job <span class="req">*</span></label>
                                <input type="text" v-model="new_members[i].member_job" placeholder="job title">

                                <label>Member phone <span class="req">*</span></label>
                                <input type="text" v-model="new_members[i].member_phone" placeholder="phone number">

                                <label>Member email <span class="req">*</span></label>
                                <input type="text" v-model="new_members[i].member_email" placeholder="email address">

                                <label>Member birthdate <span class="req">*</span></label>
                                <input
                                    type="text"
                                    v-model="new_members[i].member_birthdate"
                                    :data-object-index="i"
                                    data-toggle="datepicker"
                                    placeholder="dd/mm/yyyy">

                                <label>Member gender <span class="req">*</span></label>
                                <div class="4u 12u$(small)">
                                    <input type="radio" :id="'new_member_male'+i" v-model="new_members[i].member_gender" :name="'new_member_gender'+i" value="male">
                                    <label :for="'new_member_male'+i">Male</label>
                                </div>
                                <div class="4u 12u$(small)">
                                    <input type="radio" :id="'new_member_female'+i" v-model="new_members[i].member_gender" :name="'new_member_gender'+i" value="female">
                                    <label :for="'new_member_female'+i">Female</label>
                                </div>

                                <label>Member nationality <span class="req">*</span></label>
                                <input type="text" v-model="new_members[i].member_nationality" placeholder="member primary nationality">
                            </div>
                        </div>

                        <div class="clearfix"></div>
                        <div style="text-align: center; padding: 20px 0">
                            <div class="alert" :class="{'alert-danger': err.danger, 'alert-success': !err.danger}" style="margin-bottom: 20px;display: ">{{err.msg}}</div>
                            <button class="btn btn-primary" @click="saveMembers()">Submit</button>
                            <button class="button special" v-if="membersFormCounter < 100" @click="addMember()">Add antoher member &nbsp;<i class="icon ion-plus-round"></i></button>
                        </div>


                    </div>
                </div>
                <!-- <div class="team-details tab">
                    <div class="team-card">
                        <span class="name"><i class="icon ion-person"></i> {{team.team_name ? team.team_name : 'not set'}}</span>
                        <span class="id"><i class="icon ion-key"></i> {{team.team_id ? team.team_id : 'not set'}}</span>
                        <span class="email"><i class="icon ion-email"></i> {{team.email ? team.email : 'not set'}}</span>
                    </div>
                </div>
                <div class="team-contests tab">
                    <h3>Your contests: </h3>
                        <h4>You didn't register in any contest yet!</h4>
                    <h3>Active contest: </h3>
                    <div v-if="active_contests.length > 0">
                        <div
                            class="single-select-contest"
                            v-for="item in active_contests"
                            >
                            <img :src="api_url + '/uploads/contests/' + item.icon[0].filename" alt="" class="icon">
                            <h3>{{item.title}}</h3>
                            <span class="cash">{{item.cash}}$</span>
                        </div>
                    </div>
                    <h4 v-else>There is no active contests right now!</h4>
                </div> -->

              </div>
            </section>
        </section>
        <footer_component></footer_component>
    </div>
</template>

<script>
import navbar from '@/components/_navbar'
import footer_component from '@/components/_footer'
import hero from '@/components/_hero'

export default {
    name: 'profile',
    components: {
        navbar,
        footer_component,
        hero
    },
    data: function(){
        return {
            team: {},
            members: {},
            active_contests: [],
            api_url: my_api.url,

            new_members: [],
            membersFormCounter: 0,

            err: {
                msg: 'Something went wrong!',
                danger: null
            }
        }
    },
    created: function(){
        let token = localStorage.getItem('team_token')
        let id = localStorage.getItem('team_id')
        if(!token || !id){
            this.$router.push('/logout')
        }else{
            for(let i = 0; i < 100; i++){
                this.new_members.push({
                    member_name: '',
                    member_phone: '',
                    member_email: '',
                    member_birthdate: '',
                    member_gender: '',
                    member_job: '',
                    member_nationality: ''
                })
            }

            this.$http.get(this.api_url + '/teams/profile',{
                headers: {'Authorization': token}
            }).then(res => {
                this.team = res.body.team
                this.$http.get(this.api_url + '/teams/members/'+this.team.team_id,{
                    headers: {'Authorization': token}
                }).then(res => {
                    this.members = res.body.members
                })
            })

            this.$http.get(my_api.url+'/json/activeContests')
                .then(res => {
                    this.active_contests = res.body
                })


        }
    },
    mounted: function() {
        window.document.title =  'Profile | Grintos'
        document.body.className = 'profile-page';
    },
    beforeDestroy: function(){
        window.document.title =  'Grintos'
        document.body.className = '';
    },
    methods: {
        tab: function(name){
            $('.tab').hide()
            $('.'+name).show()
        },

        expandCard: function(e){
            let thisCard = $(e.currentTarget).closest('.member-card')
            let thisBtn  = $(e.currentTarget)
            thisBtn.toggleClass('plus minus')
            thisCard.toggleClass('expanded')
            thisCard.find('.card-body').slideToggle('fast')
        },

        showDeleteBtns: function(cond, e){
            const thisCard = $(e.currentTarget).closest('.member-card');
            const loader = thisCard.find('.loader')
            if(cond){
                thisCard.find('.delete-btns').fadeIn('fast')
                loader.fadeIn('fast')
            }else{
                thisCard.find('.delete-btns').fadeOut('fast')
                loader.fadeOut('fast')
            }
        },

        editCard: function(e){
            let thisCard = $(e.currentTarget).closest('.member-card')
            let thisBtn  = $(e.currentTarget)
            let updateBtn  = thisCard.find('.update-member')
            // rest
            // $('.card-btn.edit').removeClass('active-edit')
            // $('.old_member_card input').attr('disabled', true)
            // $('.update-member').hide()

            thisBtn.toggleClass('active-edit')
            thisCard.find('input').each(function(){
                if($(this).attr('disabled') === 'disabled'){
                    $(this).attr('disabled', false)
                    updateBtn.show()
                }else{
                    updateBtn.hide()
                    $(this).attr('disabled', true)
                }
            })
            // thisCard.toggleClass('expanded')
            // thisCard.find('.card-body').slideToggle('fast')
        },



        saveMembers: function(){
            let self = this;
            let newMembersArray = self.new_members.filter(item => {
                if(
                    item.member_name &&
                    item.member_phone &&
                    item.member_email &&
                    item.member_birthdate &&
                    item.member_gender &&
                    item.member_job &&
                    item.member_nationality
                ){
                    item.member_team_id = localStorage.getItem('team_custom_id')
                    // item.member_team_custom_id = localStorage.getItem('team_custom_id')
                    return item
                }
            })
            if( self.membersFormCounter > newMembersArray.length ){
                console.log('its a trap!!!')
                self.showAlert('Please complete all data!', true)
            }else{
                console.log('all here boss!')
                newMembersArray.forEach((item, i) => {
                    self.$http.post(my_api.url+'/members/register', item)
                        .then(res => {
                            console.log(res.body)
                            item._id = res.body.member._id
                            this.members.push(item)
                            // const index = self.new_members.findIndex(x => x.member_name === item.member_name);
                            // self.new_members.splice(index, 1)
                            self.membersFormCounter = self.membersFormCounter - 1
                        })
                        .catch(err => {
                            console.log('ERROR ', err)
                            self.showAlert('Something went wrong!', true)
                        })
                    if(i === (newMembersArray.length - 1)){
                        console.log('done', i, (newMembersArray.length - 1))
                        self.showAlert('Saved', false)
                    }
                })
            }
        },

        updateMember: function(e, id, isLeader){
            const card = $(e.target).closest('.card-body')
            const loader = card.find('.loader')
            const thisCard = $(e.currentTarget).closest('.member-card')
            const thisBtn  = thisCard.find('.card-btn.edit')
            const updateBtn  = thisCard.find('.update-member')

            loader.addClass('loading').fadeIn('fast')
            this.$http.put(my_api.url+'/edit-member?member_id='+id,{
                member_name: card.find('input[name="member_name"]').val(),
                member_phone: card.find('input[name="member_phone"]').val(),
                member_email: card.find('input[name="member_email"]').val(),
                member_gender: card.find('input[name*="member_gender"]:checked').val(),
                member_birthdate: card.find('input[name="member_birthdate"]').val(),
                member_job: card.find('input[name="member_job"]').val(),
                member_nationality: card.find('input[name="member_nationality"]').val(),
            })
            .then(res => {
                if(res.body.update){
                    setTimeout(() => {
                        loader.fadeOut('fast').removeClass('loading')
                        thisBtn.removeClass('active-edit')
                        thisCard.find('input').each(function(){
                            updateBtn.hide()
                            $(this).attr('disabled', true)
                        })
                    }, 500)
                }
                console.log(res)
            })
        },

        deleteMember: function(e, id){
            const card = $(e.currentTarget).closest('.member-card')
            this.$http.delete(my_api.url+'/edit-member?member_id='+id)
                .then(res => {
                    if(res.body.deleted){
                        card.slideUp('fast')
                    }
                })
        },

        addMember: function(){
            this.membersFormCounter += 1;
            this.initiDatepicker()
        },

        // initing datePicker
        initiDatepicker: function(){
            let self = this;
            let datepickerEl = $('[data-toggle="datepicker"]')
            datepickerEl.datepicker()
            // $('[data-toggle="datepicker"]').on('change', function(e){
            //     self.new_leader.member_birthdate = e.target.value
            // })

            setInterval(() =>{
                let datepickerEl = $('[data-toggle="datepicker"]')
                datepickerEl.datepicker()
                $('[data-toggle="datepicker"]').on('change', function(e){
                    let index = $(this).data('object-index')
                    self.new_members[index].member_birthdate = e.target.value
                })
            }, 1000)
        },

        showAlert: function(msg, isDanger) {
            this.err.msg = msg
            this.err.danger = isDanger

            $('.alert').slideDown('fast', function() {
                setTimeout(() => {
                    $('.alert').slideUp('fast')
                }, 2000)
            })
        }
    }
}
</script>

<style scoped>
ul.tabs{
    padding: 0;
    margin: 0;
    list-style-type: none;
    border-bottom: 1px solid #aaa;
    display: flex;
    user-select: none
}
ul.tabs li{
    padding: 5px 10px;
    border: 1px solid #aaa;
    display: inline-block;
    border-radius: 4px 4px 0 0;
    border-bottom: 0;
    margin-right: 5px;
    margin-left: 5px;
    transition: .14s;
    color: #333
}
ul.tabs li.active,
ul.tabs li:hover{
    cursor: pointer;
    background: rgba(255,255,255,0.05);
}

.cards-warpper, .team-details, .team-contests{
    display: none;
}

.team-details, .members-details, .team-contests{
    margin-top: 20px;
}


.cards-warpper, .team-card{
    display: block;
}
.single-member, .team-card{
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    padding: 10px 15px;
    background: #fff;
    border-radius: 5px;
    flex-basis: 48%;
    margin: 1%;
    margin-top: 0;
    color: #333;
    display: flex;
    flex-wrap: wrap
}
.single-member span{
    padding: 5px 0;
    display: block;
    flex-basis: 50%;
}
.single-member span i{
    margin-right: 10px;
}
.card-body{position: relative}
.team-card span{
    padding: 5px 0;
    display: block;
    flex-basis: 33%;
}
.team-card span i{
    margin-right: 10px;
}


.single-select-contest{
    padding: 10px;
    border: 1px solid rgba(238, 238, 238, .5);
    width: 31%;
    text-align: center;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    transition: .1s;
    cursor: pointer
}
.single-select-contest:hover{
    background: rgba(238, 238, 238, .05);
}
.single-select-contest > *{margin: auto;}
.single-select-contest img.icon{
    width: 30px;
    margin: 20px auto;
}
.single-select-contest h3{
    margin: 20px auto;
}


.team-contests > h3:nth-child(3){
    margin-top: 100px
}
.team-contests > h4{
    text-align: center;
}

input[disabled]{
    border: solid 2px rgba(255, 255, 255, 0)
}
button.update-member{
    padding: 10px 15px;
    height: initial;
    line-height: inherit;
    margin-top: 20px;
    display: none;
}
.delete-btns{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    text-align: center;
    display: none;
    z-index: 4
}
.delete-btns button{
    padding: 10px 15px;
    height: initial;
    line-height: inherit;
    margin-top: 20px;
}
.delete-btns button.btn-delete{
    background: #e74c3c;
    color: #fff;
    border-color: #e74c3c;
}

.loader{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(238, 238, 238, 0.65);
    z-index: 3;
    display: none;
}
.loader.loading:after{
    content: '';
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 3px solid #fff;
    border-right-color: #34495e;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(0deg);
    animation: rotate .4s infinite linear
}
@keyframes rotate{
    0{
        transform: translate(-50%, -50%) rotate(0deg)
    }
    100%{
        transform: translate(-50%, -50%) rotate(360deg)
    }
}

</style>



