<template>
    <!-- Banner -->
    <div id="scene" :style="`height: ${height}px`">
        <section id="banner" class="layer" data-depth="0.20" :style="`min-height: ${parseInt(height) + 100}px`">
            <div class="inner">
                <div class="contest_logo logo" v-if="icon && !logo">
                    <img :src="icon">
                </div>
                <div class="contest_logo logo with-site-logo" v-if="logo">
                    <img :src="icon">
                </div>
                <h2>{{text}} <span class="expire-date" v-if="date">Deadline: {{date | date}}</span></h2>
                <p v-if="description">
                    {{description}}
                </p>

                <router-link
                    v-if="btnUrl && (date | date !== 'Expired')"
                    class="btn btn-default btn-banner-apply"
                    tag="button"
                    :to="btnUrl"
                    >
                    {{btnText}}</router-link>
            </div>
        </section>
        <div
            class="layer"
            data-depth="0.10"
            id="bannerWallpaper"
            :style="'background-image: url('+url+')'"
            ></div>
    </div>
</template>

<script>
export default {
    name: 'hero',
    props: ['url', 'icon', 'text', 'date', 'description', 'btnText', 'btnUrl', 'logo', 'height'],
    mounted: function(){
        console.log('run parallax', typeof this.height)
        var scene = document.getElementById('scene');
        if(scene){
            var parallax = new Parallax(scene);
        }
    },
    filters: {
        date: function(value){
            if( moment(value, 'YYYY-MM-DD').format('DD/MMMM/YYYY') === 'Invalid date'){
                return 'Expired'
            }else{
                return moment(value, 'YYYY-MM-DD').format('DD/MMMM/YYYY')
            }
        },
    }
}
</script>

<style scoped>
    .expire-date{
        display: block;
        font-size: 16px;
    }
    #bannerWallpaper::after{
        background-color: rgba(0, 0, 0, 0.4);
    }
    .inner p{
        color: #fff
    }
    #banner .contest_logo.with-site-logo{
        padding: 0;
        border: 0;
    }
    #banner .contest_logo.with-site-logo img{
        width: 50px;
    }
</style>



