import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Competitions from '@/components/Competitions'
// Teams
import team_register from '@/components/Teams/Team_register'
import single_contest from '@/components/Single-contest'
import login from '@/components/Login'
import logout from '@/components/Logout'
import profile from '@/components/Teams/Profile'
import upload from '@/components/Teams/Upload'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/competitions',
      name: 'Competitions',
      component: Competitions
    },
    {
      path: '/team_register',
      name: 'team_register',
      component: team_register
    },
    {
      path: '/single_contest',
      name: 'single_contest',
      component: single_contest
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/logout',
      name: 'logout',
      component: logout
    },
    {
      path: '/profile',
      name: 'profile',
      component: profile
    },
    {
      path: '/upload',
      name: 'upload',
      component: upload
    }
  ]
})



