<template>
    <div class='page-single_contest'>
        <navbar></navbar>
        <hero
            :url="api_url + '/uploads/contests/' + this_contest_main_image"
            :icon="api_url + '/uploads/contests/' + this_contest_main_icon"
            :date="this_contest.end_date | isExpired"
            :text="this_contest.title"
            :description="this_contest.short_brife"
            btnText="Upload your files now"
            :btnUrl="'/upload?id='+this_contest._id"
            v-if="team_logged_in"
            ></hero>
        <hero
            :url="api_url + '/uploads/contests/' + this_contest_main_image"
            :icon="api_url + '/uploads/contests/' + this_contest_main_icon"
            :date="this_contest.end_date | isExpired"
            :text="this_contest.title"
            :description="this_contest.short_brife"
            btnText="Login"
            btnUrl="/login"
            v-else
            ></hero>

        <!-- Wrapper -->
        <section id="wrapper">
          <section id="contest_desc" class="wrapper spotlight style1">
              <h2 class="h-fix">brief</h2>
              <div class="inner" v-html="this_contest.brief"></div>
          </section>

          <section id="contest_slider" class="wrapper alt spotlight style2">
            <h2 class="h-fix">Media</h2>
            <div class="inner">
                <a :href="api_url+'/uploads/contests/'+image.filename" data-lightbox="image-1" class="single_image" v-for="image in this_contest.slider_images" :style="'background-image: url('+api_url+'/uploads/contests/'+image.filename+')'">
                    <div class="d-layer"><i class="icon ion-arrow-expand"></i></div>
                </a>
            </div>
            <div class="inner" v-if="this_contest.youtube_video">
                <div
                    class="video-warpper"
                    :data-url="this_contest.youtube_video">
                    <div class="dark-layer">
                        <img src="/static/images/play-btn.png" class="play-btn" alt="Play Button" />
                    </div>
                </div>
            </div>
            <div id="contest_download">
                <h2 class="h-fix">Download</h2>
                <div class="inner">
                    <div class="download-card" v-for="item in this_contest.contest_files">
                        <div class="file-name">{{item.originalname}}</div>
                        <div class="file-size">{{item.size | fileSize }}</div>
                        <a download :href="api_url+'/uploads/contests/'+item.filename"><i class="icon ion-ios-cloud-download-outline"></i></a>
                    </div>
                </div>
            </div>
          </section>

          <section id="contest_prizes" class="wrapper spotlight style3">
            <h2 class="h-fix">Prizes</h2>
            <div class="inner">
                <div class="prizes">

                    <div class="prize" v-for="item in this_contest.prizes">
                        <div class="cash">{{item.prize_cash}}$</div>
                        <div class="title">{{item.prize_st}}</div>
                        <div class="decription">{{item.prize_details}}</div>
                    </div>

                </div>
            </div>

            <div v-if="showWinners">
                <br /><br />
                <h2 class="h-fix"><img src="/static/images/party.png"> Winners <img src="/static/images/party.png"></h2>
                <div class="inner">
                    <div class="winners">
                        <div class="single-winner" v-for="(team, i) in results.winners">
                            <div class="place">{{i + 1}}<span>st</span></div>
                            <div class="text">
                                <div>
                                    <span class="key">Team: </span>
                                    <span>{{team.team_name}}</span>
                                </div>
                                <div>
                                    <span class="key">Members: </span>
                                    <span>
                                        <span v-for="member of team.members">{{member.member_name}}</span>
                                    </span>
                                </div>
                            </div>
                            <!-- <div class="score">{{team.reviews}}<span>/100</span></div> -->
                            <div
                                v-if="team.cover"
                                class="cover" :style="`background-image: url('${api_url}/contest_files/${team.cover.filename}')`"></div>
                                <div
                                    v-else
                                    class="cover">
                                        <span>No cover</span>
                                    </div>
                        </div>
                    </div>
                </div>


                <br /><br />
                <h2 class="h-fix"><img src="/static/images/contest_icon/theatre-column.svg" style="vertical-align: middle"> Golden Mentions <img src="/static/images/contest_icon/theatre-column.svg" style="vertical-align: middle"></h2>
                <div class="inner">
                    <div class="gold_mentions">
                        <div class="single-gold_mentions" v-for="(team, i) in results.gold_mentions">
                            <span class="name">
                                {{team.team_name}}
                            </span>
                            <div
                                v-if="team.cover"
                                class="cover" :style="`background-image: url('${api_url}/contest_files/${team.cover.filename}')`"></div>
                            <div
                                v-else
                                class="cover">
                                    <span>No cover</span>
                                </div>
                        </div>
                    </div>
                </div>
            </div>
          </section>

          <!-- <section id="contest_download" class="wrapper alt spotlight style4">
              <h2 class="h-fix">Download</h2>
              <div class="inner">
                  <div class="download-card" v-for="item in this_contest.contest_files">
                      <div class="file-name">{{item.originalname}}</div>
                      <div class="file-size">{{item.size | fileSize }}</div>
                      <a download :href="api_url+'/uploads/contests/'+item.filename"><i class="icon ion-ios-cloud-download-outline"></i></a>
                  </div>
              </div>
          </section> -->

          <section id="contest_jury" class="wrapper alt spotlight style4">
            <h2 class="h-fix">Jury</h2>
            <div class="inner">
                <div
                    class="judge-card"
                    v-for="item in judges"
                    :class="{'hovered': item.judge_brief}"
                    :style="'background-image: url('+api_url+'/uploads/judges/'+item.judge_avatar.filename+')'"
                    >
                    <div class="info">
                        <div class="name">{{item.judge_username}}</div>
                        <div class="job" v-if="item.judge_brief">{{item.judge_brief}}</div>
                    </div>
                </div>
            </div>
          </section>

          <section id="contest_calendar" class="wrapper spotlight style5">
            <h2 class="h-fix">FAQ</h2>
            <div class="inner">
                <div class="faqs">
                    <div class="single-faq" v-for="item in this_contest.faq">
                        <div class="head" @click="toggleFaq($event)">
                            <div class="text">{{item.faq_q}}</div>
                            <span class="icon ion-plus-round"></span>
                            <span class="icon ion-minus-round"></span>
                        </div>
                        <div class="body">{{item.faq_a}}</div>
                    </div>
                </div>
            </div>
            <div class="someHeightPlease"></div>
            <h2 class="h-fix">Calendar</h2>
            <div class="inner">
                <div class="single_calendar" v-for="item in this_contest.calendar">
                    <div class="dates">
                        <div class="start-date">
                            {{item.calendar_start_date | getMonthandDay}} {{item.calendar_start_date | getYear}}
                            <span>(start date)</span>
                        </div>
                        <div class="end-date">
                            {{item.calendar_end_date | getMonthandDay}} {{item.calendar_end_date | getYear}}
                            <span>(end date)</span>
                        </div>
                    </div>
                    <div class="title">
                        {{item.calendar_title}}
                        <span class="fee">{{item.calendar_fee}}</span>
                    </div>
                </div>

            </div>
          </section>


          <section id="contest_partners" class="wrapper alt spotlight style3">
            <h2 class="h-fix">Partners</h2>
            <div class="inner">


            <div class="clients-cards">
                <a v-for="item in partners" v-if="item" :href="item.link" class="card" >
                    <img :src="api_url+'/uploads/partners/'+item.logo.filename" alt="">
                    <h4>{{item.name}}</h4>
                </a>
            </div>

            </div>
          </section>



        </section>
        <footer_component></footer_component>
    </div>
</template>

<script>
import navbar from './_navbar'
import footer_component from './_footer'
import hero from './_hero'

export default {
    name: 'single_contest',
    components: {
        navbar,
        footer_component,
        hero
    },
    data: function(){
        return {
            this_contest: {},
            this_contest_main_image: '',
            this_contest_main_icon: '',
            judges: {},
            partners: [],
            api_url: my_api.url,
            team_logged_in: false,
            results: [],
            showWinners: false
        }
    },
    created: function(){
        let team_token = localStorage.getItem('team_token')
        if(team_token){
            this.team_logged_in = true
        }
        var id = this.getParameterByName('id')
        this.$http.get(my_api.url + '/json/contest/'+id)
            .then(res => {
                this.this_contest = res.body
                this.this_contest_main_image = res.body.main_image[0].filename
                this.this_contest_main_icon = res.body.icon[0].filename
                window.document.title =  this.this_contest.title + ' | Grintos'

                if(this.showWinnersCheck(this.this_contest.winners_date)){
                    this.showWinners = true
                    this.getWinners()
                }

                // get partners
                this.this_contest.partners.forEach((item, i)=> {
                    this.$http.get(my_api.url + '/json/getContestPartners/'+item)
                        .then(res => {
                            this.partners.push(res.body)
                        })
                })

            })
        // get judges
        this.$http.get(my_api.url + '/json/getContestJudges/'+id)
            .then(res => {
                this.judges = res.body
            })
    },
    beforeDestroy: function(){
        window.document.title =  'Grintos'
        document.body.className = '';
    },
    mounted: function(){
        document.body.className = 'single-contest-page';
        // Append the video thumbnail on load
        let self = this
        setTimeout(function(){
            $('.video-warpper').is(function(){
                var id = self.get_video_id($(this).data('url')) // Video ID
                $(this).append( '<img class="video-thump" src="https://img.youtube.com/vi/'+ id +'/0.jpg" alt="" />' ); // Append the image
            });
            // Append the video iframe on user's click on the thumbnail
            $('.video-warpper').click(function(){
                var id = self.get_video_id($(this).data('url')) // Get the ID
                $(this).append(
                    '<iframe src="https://www.youtube.com/embed/' + id + '?showinfo=0&iv_load_policy=3&modestbranding=1&autoplay=1&rel=0"></iframe>'); // Appending the iframe
                $(this).find('.dark-layer').fadeOut() // Remove the dark layer
                $(this).find('.video-thump').fadeOut() // Remove the video thumbnail
            });
        }, 500)
    },
    methods: {
        showWinnersCheck: function(val){
            let date = new Date(val)
            let now = new Date()

            if(date >= now) {
                return false
            } else {
                return true
            }
        },
        getWinners: function(){
            this.$http.get(`${my_api.url}/winners?id=${this.getParameterByName('id')}`)
                .then(res => {
                    console.log(res.body)
                    this.results = res.body
                })
        },
        getParameterByName: function(name, url) {
            if (!url) {
                url = window.location.href;
            }
            name = name.replace(/[\[\]]/g, "\\$&");
            var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
                results = regex.exec(url);
            if (!results) return null;
            if (!results[2]) return '';
            return decodeURIComponent(results[2].replace(/\+/g, " "));
        },
        get_video_id: function(url){
            var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#\&\?]*).*/;
            var match = url.match(regExp);
            return (match&&match[7].length==11)? match[7] : false;
        },
        toggleFaq: function(e){
            let thisFaq = $(e.target)
            thisFaq.closest('.single-faq').toggleClass('active')
        },
        isExpired: function(value) {
            let date = new Date(value)
            let now = new Date()


            if(date >= now) {
                return false
            } else {
                return true
            }
        }
    },
    filters: {
        getMonthandDay: function(value) {
            return moment(value, 'YYYY-MM-DD').format('DD MMMM');
        },
        getYear: function(value) {
            return moment(value, 'YYYY-MM-DD').format('YYYY');
        },
        fileSize: function(value) {
            let kb = Math.floor(value / 1024);
            if( kb > 1023 ){
                return Math.floor(kb / 1024) + 'mb'
            }else{
                return kb + 'kb'
            }
        },
        isExpired: function(value) {
            let date = new Date(value)
            let now = new Date()


            if(date >= now) {
                return value
            } else {
                return 'Expired'
            }
        }
    }
}
</script>

<style>

</style>



